*, :before, :after{
	box-sizing: border-box;
}

html, body{
	height: 100%;
}

body {
	font-family: $bodyFont;
	color: $bodyTextColor;
	background-color: $bodyBackgroundColor;
	overflow-y: scroll;
	-webkit-font-smoothing: antialiased; // standardise font apgreenance between OS'
	-moz-osx-font-smoothing: grayscale;
}

main {
	background-color: white;
	display: flex;
	flex-direction: column;
	flex-grow: 1;
}
